<template>
  <transition name="vux-toast-fade">
    <div v-show="isShow"
         :class="[position==='bottom'?'bottom':'',position==='upper'?'upper':'',]"
         class="vux-toast-container"
    >
      <div class="vux-toast-tip">{{ content }}</div>
    </div>
  </transition>
</template>

<script>

export default {
  name: "toast",
  data() {
    return {
      isShow: false
    }
  },
  props: {
    content: {
      type: String,
      default: ""
    },
    duration: {
      type: Number | String,
      default: 3000
    },
    position: {
      type: String,
      default: "upper"
    }
  },
  methods: {
    show() {
      this.isShow = true;
      if (this.duration) {
        setTimeout(() => {
          this.hide();
        }, this.duration);
      }
    },
    hide() {
      this.isShow = false;
    }
  }
};
</script>

<style lang="less" scoped>
.vux-toast-container {
  padding: 6px 15px;
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2000;
  transform: translate(-50%, -50%);
}

.vux-toast-container.bottom {
  top: 90%;
}

.vux-toast-container.upper {
  top: 20%;
}

.vux-toast-tip {
  font: 12px/1.5 sans-serif;
  color: #fff;
}

.vux-toast-fade-enter-active {
  animation: toast-in var(--vux-duration-base);
}

.vux-toast-fade-leave-active {
  animation: toast-out var(--vux-duration-base);
}

//@keyframes toast-in {
//  0% { opacity: 0; }
//  100% { opacity: 1; }
//}
//
//@keyframes toast-out {
//  0% { opacity: 1; }
//  100% { opacity: 0; }
//}
@keyframes toast-in {
  0% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.9);
  }
  15% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(1.05); /* 初始放大 */
  }
  40% {
    opacity: 0.2;
    transform: translate(-50%, -50%) scale(1.02); /* 逐渐缩小 */
  }
  70% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}

@keyframes toast-out {
  0% {
    opacity: 1;
    transform: translate(-50%, -50%);
  }
  25% {
    opacity: 0.7;
    transform: translate(-50%, -50%) scale(0.95); /* 轻微放大 */
  }
  50% {
    opacity: 0.4;
  }
  75% {
    opacity: 0.1;
    transform: translate(-50%, -50%) scale(0.85); /* 轻微放大 */
  }
  100% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.75); /* 完全透明并稍微缩小 */
  }
}
</style>
